import { KANJI_KANA_REGEX } from '../constants';
import isEmpty from '../utils/isEmpty';

/**
 * Test if `input` is [Kanji](https://en.wikipedia.org/wiki/Kanji) and/or [Kana](https://en.wikipedia.org/wiki/Kana) like â€œã€Œæ³£ãè™«ã€â€
 * Includes Japanese full-width punctuation ranges
 * @param  {String} [input=''] text
 * @return {Boolean} true if all [Kanji](https://en.wikipedia.org/wiki/Kanji) and/or [Kana](https://en.wikipedia.org/wiki/Kana)
 * @example
 * isKanjiKana('æ³£ãè™«')
 * // => true
 * isKanjiKana('ã‚ã‚¢')
 * // => true
 * isKanjiKana('æ³£ãè™«ã€‚ï¼ã€œ') // Full-width punctuation
 * // => true
 * isKanjiKana('æ³£ãè™«.!~') // Half-width / Latin punctuation
 * // => false
 * isKanjiKana('æ³£ãè™«A')
 * // => false
 * isKanjiKana('A')
 * // => false
 */
function isKanjiKana(input = '') {
  if (isEmpty(input)) return false;
  return [...input].every((char) => KANJI_KANA_REGEX.test(char));
}

export default isKanjiKana;



